
/* Nav */

.menu {
  display: flex;
  position: fixed;
  width: 100vw;
  z-index: 3; 
  background-color: #f8f8f8;
}

.menu-logo-container {
  flex-grow: 1;
  z-index: -2; 
}

.menu-logo {
  width: 25px;
  height: 25px;
  padding: 15px;
}

.menu-icon {
  width: 30px;
  height: 30px;
  padding: 15px;
}

.mobile-container-list {
  background-color: #242424;
  padding-top: 220px;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0px;
  left: 0px;
  text-align: center;
  z-index: -1;
  animation: opacity 300ms ease-in-out normal;   
}

.mobile-container-list > li {
  list-style: none;
  margin-bottom: 5px;
}

.mobile-element {
  color: white;
  text-decoration: none;
  font-size: 3.3rem;
}

.mobile-element:hover {
  text-decoration: line-through;
}

.menu-list{
  display: none;
  visibility: hidden;
  list-style: none;
  text-align: center;
}


/* No mobile menu */

.menu-element {
  text-decoration: none;
  font-size: 1.35rem;
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #242424;
  margin: 0 10px;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.menu-element,
.menu-element:after,
.menu-element:before {
transition: all .5s;
}

.menu-element:after {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 1px;
  content: '.';
  color: transparent;
  background: #f8f8f8;
  visibility: none;
  opacity: 0;
  z-index: -1;
}


.menu-element:hover {
  font-size: 1.7rem;
  font-weight: 500;
}

nav ul li a:hover:after {
  opacity: 1;
  visibility: visible;
  height: 100%;
}


/* cover */

.cover {
display: flex;
flex-direction: row;
height: 100vh;
opacity: 0;
transition: opacity .5s .25s ease-out;
padding-top: 25px;
}

.cover.is-visible {
  opacity: 1;
}

.cover-container-text {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 30px 0px 30px;
  text-align: left;
}

.cover-title {
  font-size: 1.6rem;
  color: #c53a13;
  letter-spacing: 1px;
  font-weight: 800;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
  transition-delay: 200ms;
  padding-bottom: 10px;
}

.cover-title.is-visible {
  transform: translate(0px, -25px);   
} 

.cover-paragraph {
  font-size: 3.4rem;
  line-height: 41px;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
  transition-delay: 250ms;
}

.cover-paragraph.is-visible{
  transform: translate(0px, -25px);   
}

.cover-social {
  display: flex;
  margin-top: 50px;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.social-icon {
  width: 18px;
  margin-right: 20px;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.social-icon.is-visible {
  transform: translate(0px, -25px);   
  transition-delay: 300ms;       
}

/* Projects section */

.image-project {
  text-align: center;
  position: relative;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.image-project-right {
  text-align: right;
  position: relative;
  opacity: 0;
  transition: opacity .5s .25s ease-out; 
}

.image-project-right.is-visible {
  opacity: 1;
}

.image-project.is-visible {
  opacity: 1;
}

.image-projects {
  width: 95%;
}

.title-project {
  position: absolute;
  font-size: 3.5rem;
  top: -5%;
  left: 45%;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project.is-visible {
  transform: translate(0px, -15px);   
  transition-delay: 300ms;  
}

.title-project-left {
  position: absolute;
  font-size: 3.5rem;
  top: 2%;
  left: 5%;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project-left.is-visible {
  transform: translate(0px, -15px);   
  transition-delay: 300ms;  
}

.link-project {
  position: absolute;
  top: 6%;
  left: 60%;
  font-weight: 700;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 1.5rem;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project.is-visible {
  transform: translate(0px, -15px);   
  transition-delay: 500ms; 
}

.link-project:hover {
  text-decoration: underline;
}

.link-project-left {
  position: absolute;
  top: 12%;
  left: 32%;
  font-weight: 700;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 1.5rem;
  transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project-left.is-visible {
  transform: translate(0px, -15px);   
  transition-delay: 500ms; 
}

.link-project-left:hover {
  text-decoration: underline;
}


.grey-color {
  color: #242424;
}



/* Media Queries for IPAD */

@media (min-width: 768px) and (max-width: 1024px) {
  
  .mobile-container-list {
      padding-top: 280px; 
  }

  .cover-container-text {
      padding-left: 60px;
  }

  .title-project {
      font-size: 5.4rem;
      left: 60%;
      top: 5%;
  }
     
  .title-project-left {
      font-size: 5.4rem;
      top: 5%;
      left: 5%;
  }
  
  .link-project {
      top: 14%;
      left: 55%;
      font-size: 2rem;
  }
  
  .link-project-left {
      top: 14%;
      left: 47%;
      font-size: 2rem;
  }
}

/* Media Queries for Desktop */

@media (min-width: 1025px) and (max-width: 1280px) {

  .menu {
      position: fixed;
  }

  .menu-icon {
      display: none;
      visibility: hidden;
  }
  
  .menu-list {
      display: flex;
      visibility: inherit;
      padding: 35px 20px 20px 20px;
  }

  .menu-list li {
      list-style: none;
  }

  .cover-container-text {
      padding-left: 60px;
  }
  
  .cover-paragraph {
      font-size: 3.8rem;
      padding-right: 100px;
      line-height: 54px;
  }

  .title-project {
      font-size: 5.4rem;
      left: 65%;
      top: 5%;
  }
     
  .title-project-left {
      font-size: 5.4rem;
      top: 5%;
      left: 5%;
  }
  
  .link-project {
      top: 15%;
      left: 62%;
      font-size: 2rem;
  }
  
  .link-project-left {
      top: 16%;
      left: 37%;
      font-size: 2rem;
  }

  .image-projects {
      width: 60%;
  }
}

@media (min-width: 1281px) {

  .menu {
      position: fixed;
  }

  .menu-icon {
      display: none;
      visibility: inherit;
  }
  
  .menu-list{
      display: flex;
      visibility: inherit;
      padding: 35px 20px 20px 20px;
  }

  .menu-list li {
      list-style: none;
  }

  .cover-container-text {
      padding-left: 60px;
  }

  .cover-paragraph {
      font-size: 3.8rem;
      padding-right: 250px;
      line-height: 54px;
  }

  .title-project {
      font-size: 5.4rem;
      left: 65%;
      top: 5%;
  }
     
  .title-project-left {
      font-size: 5.4rem;
      top: 5%;
      left: 5%;
  }
  
  .link-project {
      top: 13%;
      left: 62%;
      font-size: 2rem;
  }
  
  .link-project-left {
      top: 13%;
      left: 27%;
      font-size: 2rem;
  }

  .image-projects {
      width: 65%;
  }
}


/* KeyFrames */

@keyframes opacity {
  0% {
    transform: translate(100px, -250px);
    opacity: 0;
  }
  50%{
    transform: translate(0px, 0px);
    opacity: 0.5;
  }
  100% {
    transition: transform 0.2s;
    opacity: 1;
  }
}





